.bread-list{
  margin-top: 36px;
}

.breadcrumb{
  margin: 0;
  padding: 0;
  list-style: none;

  li{
    display: inline;
    list-style: none;
    font-weight: bold;

    &:after{
      content:">";
      padding: 0 0.2em;
      color:#555;
    }

    &:last-child:after{
      content:'';
    }
  }
}