@charset "UTF-8";
html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  font-family: "Yu Gothic", "游ゴシック", YuGothic, "游ゴシック体", "ヒラギノ角ゴ Pro W3", "メイリオ", sans-serif;
  color: #1b2224;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

section.anchor {
  display: block;
  padding-top: 70px;
  margin-top: -70px;
}

.section-wrapper {
  max-width: 940px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 20px;
  padding-left: 20px;
}

.section-ttl {
  font-size: 3.6rem;
  font-weight: bold;
  color: #1b224c;
}

.bread-list {
  margin-top: 36px;
}

.breadcrumb {
  margin: 0;
  padding: 0;
  list-style: none;
}

.breadcrumb li {
  display: inline;
  list-style: none;
  font-weight: bold;
}

.breadcrumb li:after {
  content: ">";
  padding: 0 0.2em;
  color: #555;
}

.breadcrumb li:last-child:after {
  content: '';
}

.cta-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 260px;
  height: 60px;
  font-size: 1.6rem;
  color: #FFF;
  font-weight: bold;
  margin-right: auto;
  margin-left: auto;
  background-color: #f5a623;
  border-radius: 31px;
  margin-top: 51px;
}

.contact-btn {
  display: block;
  font-size: 1.6rem;
  color: #fff;
  text-align: center;
  width: 96px;
  margin-right: auto;
  margin-left: auto;
  border-bottom: 1px solid #FFF;
  margin-top: 22px;
  opacity: 0.8;
}

.cta-btn2 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 50px;
  border-radius: 50px;
  border: 2px solid #FFF;
  font-size: 1.6rem;
  color: #FFF;
}

#header {
  position: fixed;
  display: flex;
  align-items: center;
  background-color: #FFF;
  width: 100%;
  height: 80px;
  padding: 0 20px;
  z-index: 1000;
}

#header .header-inner {
  width: 100%;
  display: flex;
  align-items: center;
}

#header .header-inner h1 {
  margin-right: 28px;
  width: 136px;
  height: 27px;
  display: flex;
  align-items: flex-end;
}

#header .header-inner h1 > img {
  object-fit: cover;
}

#header .header-inner .header-nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

#header .header-inner .header-nav .nav-list {
  display: flex;
  align-items: center;
}

#header .header-inner .header-nav .nav-list li {
  margin-right: 28px;
}

#header .header-inner .header-nav .nav-list li > a {
  line-height: 1.8;
  font-size: 16px;
  color: #1b224c;
  font-weight: bold;
}

#header .header-inner .header-nav .nav-list-r {
  display: flex;
  align-items: center;
}

#header .header-inner .header-nav .nav-list-r li:nth-child(1) {
  text-align: right;
  margin-right: 23px;
}

#header .header-inner .header-nav .nav-list-r li:nth-child(1) p:nth-of-type(1) {
  font-size: 12px;
  line-height: 1.2;
  margin-bottom: 10px;
}

#header .header-inner .header-nav .nav-list-r li:nth-child(1) p:nth-of-type(2) {
  font-size: 16px;
  font-weight: bold;
  line-height: 1.6;
}

#header .header-inner .header-nav .nav-list-r li:nth-child(1) p:nth-of-type(2) i {
  margin-right: 2px;
}

#header .header-inner .header-nav .nav-list-r li:nth-child(2),
#header .header-inner .header-nav .nav-list-r li:nth-child(3) {
  border-radius: 5px;
}

#header .header-inner .header-nav .nav-list-r li:nth-child(2) {
  background-color: #f5a623;
  margin-right: 10px;
}

#header .header-inner .header-nav .nav-list-r li:nth-child(3) {
  background-color: #1b224c;
}

#header .header-inner .header-nav .nav-list-r li > a {
  width: 137px;
  height: 50px;
  display: block;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  line-height: 1.7;
  color: #FFF;
  font-weight: bold;
}

#header .nav-wrapper {
  display: flex;
  justify-content: space-between;
}

#main {
  padding-top: 80px;
}

#main .kv-wrapper {
