}

#main #section-feature .section-ttl {
  margin-top: 59px;
  text-align: center;
}

#main #section-feature .section-inner-feature .feature-list {
  display: flex;
  justify-content: space-between;
  margin-top: 59px;
}

#main #section-feature .section-inner-feature .feature-list .feature-text-no {
  font-size: 12px;
  line-height: 1.6;
  color: #FFF;
  background-color: #f5a623;
  width: 90px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

#main #section-feature .section-inner-feature .feature-list .feature-text-ttl {
  font-size: 2.6rem;
  line-height: 1.5;
  font-weight: bold;
  margin-top: 35px;
}

#main #section-feature .section-inner-feature .feature-list .feature-text-content {
  font-size: 1.6rem;
  line-height: 1.6;
  margin-top: 39px;
  width: 500px;
  height: 70px;
}

#main #section-feature .section-inner-feature .feature-list:nth-child(2) {
  flex-direction: row-reverse;
}

#main #section-price .section-inner-price {
  padding-top: 61px;
  padding-bottom: 53px;
  width: 906px;
  height: 217px;
  background-image: url(/asset/price@2x.png);
  background-size: cover;
  margin-top: 93px;
}

#main #section-price .section-inner-price .section-ttl {
  text-align: center;
  font-size: 2.6rem;
  color: #fff;
}

#main #section-price .section-inner-price .cta-btn2 {
  margin-right: auto;
  margin-left: auto;
  margin-top: 27px;
}

#main #section-jirei {
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: #1b224c;
  color: #fff;
  margin-top: 39px;
}

#main #section-jirei .section-inner-jirei .section-ttl {
  text-align: center;
}

#main #section-jirei .section-inner-jirei .jirei-list {
  display: flex;
  justify-content: space-between;
}

#main #section-jirei .section-inner-jirei .jirei-list .jirei-item {
  margin-top: 137px;
  width: 280px;
}

#main #section-jirei .section-inner-jirei .jirei-list .jirei-item .jirei-text {
  font-size: 1.6rem;
  font-weight: bold;
  line-height: 1.8;
