  justify-content: center;
  background-color: #FFF;
}

#main #section-jirei .section-inner-jirei .jirei-list .jirei-item .jirei-genre1 {
  float: left;
  width: 100px;
  height: 32px;
  color: #1b224c;
  background-color: #FFF;
  display: flex;
  align-items: center;
  padding-left: 10px;
}

#main #section-jirei .section-inner-jirei .jirei-list .jirei-item .jirei-genre2 {
  float: right;
  width: 172px;
  height: 32px;
  color: #1b224c;
  background-color: #FFF;
  display: flex;
  align-items: center;
  padding-left: 10px;
}

#main #section-jirei .section-inner-jirei .jirei-list .jirei-item .jirei-genre3 {
  width: 280px;
  height: 32px;
  margin-top: 58px;
  color: #1b224c;
  background-color: #FFF;
  display: flex;
  align-items: center;
  padding-left: 10px;
}

#main #section-flow {
  padding-top: 58px;
  padding-bottom: 75px;
}

#main #section-flow .section-inner-flow .section-ttl {
  text-align: center;
  margin-bottom: 53px;
}

#main #section-flow .section-inner-flow .flow-table .table-row {
  margin-bottom: 8px;
  display: flex;
}

#main #section-flow .section-inner-flow .flow-table .table-row .table-item-no {
  width: 64px;
  height: 64px;
  color: #FFF;
  background-color: #f5a623;
  font-size: 1.6rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

#main #section-flow .section-inner-flow .flow-table .table-row .table-item-ttl {
  width: 214.5px;
  height: 64px;
  background-color: #f8f8f8;
  color: #1b224c;
  font-size: 1.8rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  padding-left: 21px;
  position: relative;
}

#main #section-flow .section-inner-flow .flow-table .table-row .table-item-ttl::after {
  position: absolute;
  left: 100%;
  content: "";
  width: 2px;
  height: 29px;
  background-color: #C3C3C3;
}

#main #section-flow .section-inner-flow .flow-table .table-row .table-item-text {
  width: 620.5px;
  height: 64px;
  background-color: #f8f8f8;
  color: #1b224c;
  font-size: 1.6rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  padding-left: 53.5px;
}

#main #section-question .section-inner-question .section-ttl {
  text-align: center;
}

#main #section-question .section-inner-question .question-item {
  margin-top: 32.5px;
}

#main #section-question .section-inner-question .question-item .question-item-ttl {
  font-size: 1.6rem;
  color: #1b224c;
  font-weight: normal;
  height: 74px;
  border-top: #1b224c solid 2px;
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
}

#main #section-question .section-inner-question .question-item .question-item-ttl::before, #main #section-question .section-inner-question .question-item .question-item-ttl::after {
  position: absolute;
  content: "";
  height: 3px;
  width: 25px;
  background-color: #1b224c;
  left: 90%;
}

#main #section-question .section-inner-question .question-item .question-item-ttl::before {
  transform: rotate(-90deg);
  transition: 1s;
}

#main #section-question .section-inner-question .question-item .active::before {
  transform: rotate(0deg);
  transition: 1s;
}

#main #section-question .section-inner-question .question-item .question-item-text {
  display: none;
  font-size: 1.6rem;
  color: #1b224c;
  height: 65px;
  background-color: #f8f8f8;
  align-items: center;
  padding-left: 25.5px;
  margin-bottom: 16px;
}

#main .section-2col {
